import Vue from "vue"
import Vuex from "vuex"
import mutations from "./mutations"
import actions from "./actions"
import getters from "./getters"

Vue.use(Vuex)

const state = {
    userInfo : "",//
	loading : false,//loading
	good : [],
	star : [],
	view : [],
	newPro : {
		title : '',
		described : '',
		id : '',
		cover : 'default_cover.jpg',
		file : ''
	}
}

export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations
})